<template>
    <div>
        <h4>COURSES</h4>
        <b-table striped hover :items="courses">
          <template  #cell(code)="data">
          <a :href="`/course/${data.index}`">{{ data.value }}</a>
      </template>
        </b-table>
    </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {}
  },
  computed: {
    ...mapState([
      'courses'
    ])
  }
}
</script>
