<template>
    <div>
        <h4>USERS</h4>
        <b-table striped hover :items="users">
          <template  #cell(id)="data">
          <a :href="`/user/${data.index}`">{{ data.value }}</a>
      </template>
      </b-table>
    </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {}
  },
  computed: {
    ...mapState([
      'users'
    ])
  }
}
</script>
