<template>
<div class="user">
 <b-row class="justify-content-md-center" style="margin-top:20px;">
     <b-col lg="4">
         <b-card bg-variant="light" text-variant="dark" :title='currentUser.name' class="text-center">
        <b-card-text>
            <b-card-text><h6 style="color:blue;">User ID:{{ currentUser.id }}</h6></b-card-text>
         <b-card-text>User Type:{{ currentUser.role }}</b-card-text>
         <b-card-text><em>Sex: {{ currentUser.gender }}</em></b-card-text>
        </b-card-text>
        <b-link class="nav-link nav-link-icon" to="#">
          <span class="nav-link nav-link-inner--text">Manage User</span>
        </b-link>
</b-card>
     </b-col>
 </b-row>

</div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
      currentUser: {}
    }
  },
  computed: {
    ...mapState([
      'users'
    ])
  },
  mounted () {
    const currentUser = this.users[this.$route.params.id]
    this.currentUser = currentUser
  }
}
</script>
