<template>
<div class="course">
 <b-row class="justify-content-md-center" style="margin-top:20px;">
     <b-col lg="4">
         <b-card bg-variant="light" text-variant="dark" :title='currentCourse.code+" - "+currentCourse.title' class="text-center">
        <b-card-text>
         <b-card-text>{{ currentCourse.description }}</b-card-text>
         <b-card-text><em>Credit Unit: {{ currentCourse.credit }}</em></b-card-text>
        </b-card-text>
        <b-link class="nav-link nav-link-icon" to="#">
          <span class="nav-link nav-link-inner--text">New Enrolment</span>
        </b-link>
        <b-link class="nav-link nav-link-icon" to="#">
          <span class="nav-link nav-link-inner--text">Update Course</span>
        </b-link>
        <b-link class="nav-link nav-link-icon" to="#">
          <span class="nav-link nav-link-inner--text">Back</span>
        </b-link>
</b-card>
     </b-col>
 </b-row>

</div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
      currentCourse: {}
    }
  },
  computed: {
    ...mapState([
      'courses'
    ])
  },
  mounted () {
    const currentCourse = this.courses[this.$route.params.id]
    this.currentCourse = currentCourse
  }
}
</script>
