<template>
  <div class="users">
    <b-row class="justify-content-md-center" style="margin:0; padding:0; ">
      <Users/>
    </b-row>
    <b-row class="justify-content-md-center">
        <div class="d-flex flex-row">
            <div class="p-2">
              <b-button @click="toggleUserForm" variant="secondary">add new</b-button>
            </div>
            <div class="p-2">
              <b-button class="main-navigation-button" variant="secondary"
                >update</b-button
              >
            </div>
            <div class="p-2">
              <b-button class="main-navigation-button" variant="danger"
                >delete</b-button
              >
            </div>
          </div>
    </b-row>
    <b-container>
    <b-form @submit.prevent="handleSubmit" v-if="showUserForm" style=" padding:20px;">
      <b-form-group id="input1" label="User id" label-for="id">
        <b-form-input
          id="id"
          type="text"
          v-model="formData.code"
          required
          placeholder="enter User id"
        />
      </b-form-group>
      <b-form-group id="input2" label="User Name" label-for="name">
        <b-form-input
          id="name"
          type="text"
          v-model="formData.title"
          required
          placeholder="enter user name"
        />
        </b-form-group>
        <b-form-group id="input3" label="Gender" label-for="gender">
        <b-form-input
          id="gender"
          type="text"
          v-model="formData.credit"
          required
          placeholder="enter gender"
        />
      </b-form-group>
      <b-form-group id="input4" label="User Role" label-for="role">
        <b-form-input
          id="role"
          type="text"
          v-model="formData.description"
          required
          placeholder="enter user role"
        />
      </b-form-group>
      <b-button type="submit" varient="primary">Submit</b-button>
      <b-button type="reset" varient="Danger">Reset</b-button>
    </b-form>
    </b-container>
  </div>
</template>
<script>
import Users from '@/components/Users.vue'
export default {
  components: {
    Users
  },
  data () {
    return {
      showUserForm: false,
      formData: {
        id: '',
        name: '',
        gender: '',
        role: ''
      }
    }
  },
  methods: {
    toggleUserForm () {
      this.showUserForm = !this.showUserForm
    }
  }
}
</script>
